h1,
h2,
h3,
h4 {
  color: var(--headings);
}

h1 {
  padding-top: 20px;
  display: flex;
  justify-content: center;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-word;
  max-width: 30em;
  margin: 0 auto;
}
@supports (-webkit-line-clamp: 2) {
  h1 {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: initial;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
}